<template>
  <el-card class="box-card">
    <el-form ref="dataForm" :model="param" :rules="rule" label-width="80px">
      <el-form-item label="label" prop="label">
        <el-input v-model="param.label" placeholder="label"></el-input>
      </el-form-item>
      <el-form-item label="xCoord" prop="xCoord">
        <el-input type="number" v-model="param.xCoord" placeholder="xCoord"></el-input>
      </el-form-item>
      <el-form-item label="yCoord" prop="yCoord">
        <el-input type="number" v-model="param.yCoord" placeholder="yCoord"></el-input>
      </el-form-item>
      <el-form-item label="space" prop="space">
        <el-input type="number" v-model="param.space" placeholder="space"></el-input>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="onSubmit">confirm</el-button>
      </el-form-item>
    </el-form>
  </el-card>
</template>

<script>
export default {
  data() {
    return {
      param: {
        label: '',
        xCoord: '',
        yCoord: '',
        space: '',
      },
      rule: {
        label: [{required: true, message: "please fill in label", trigger: 'blur'}],
        xCoord: [{required: true, message: "please fill in xCoord", trigger: 'blur'}],
        yCoord: [{required: true, message: "please fill in yCoord", trigger: 'blur'}],
        space: [{required: true, message: "please fill in space", trigger: 'blur'}],
      }
    }
  },
  methods: {
    onSubmit() {
      this.$refs.dataForm.validate((valid) => {
        if (valid) {
          this.$axios.post('/locations/create', this.param).then(res => {
            if (res.success) {
              this.$message({
                message: 'successfully added',
                type: 'success'
              });
              this.$router.back();
            } else {
              this.$message.error(res.message);
            }
          })
        }
      });
    }
  }
}
</script>

